*{
  text-align: center;
  box-sizing: border-box;

}

.App {
  background-image: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
 
}

.App > h1{
border-radius: 2rem;
padding: 1rem;


}

.cap{
  color: yellow;
  text-transform: uppercase;
  font-size: 250%;
  
}

.section{
  display: flex;
  height: 30vh;
  
 
}

.sub-section{
  width: 50%;
  float: left;
  margin-left: 3rem;
  margin-right: 3rem;
  padding: .5rem;
  border: 2px solid #130f40;
  border-radius: 1rem;
  background-color: rgba(0, 0, 0, 0.121);
  font-size: smaller;
} 
.sub-section >h1{
  
}

.btn{

  margin: 1rem;
  position:relative;
  display:inline-block;
  outline:none;
  border: 2px solid #130f40;
  cursor:pointer;
  font-weight:500;
  color:#130f40;
  text-transform:uppercase;
  letter-spacing:2px;
  padding:25px 60px;
  border-radius: 2rem;
 

}
.btn:hover{
  color:white;
  background-color:#130f40 ;
  border: 2px solid white;
}
.result{
  border: 1px solid rgba(0, 255, 255, 0.009);
  height: 5rem;
  color: yellow;
  font-size: 300%;
  text-shadow: 4px 4px 2px rgba(0,0,0,0.6);
}
